import { Directive } from '@angular/core';

import {strongPasswordValidator} from '../validators/strongPasswordValidator';

import {NG_VALIDATORS, Validator, AbstractControl, ValidationErrors} from '@angular/forms';

@Directive({
  selector: '[appStrongPasswordValidator]',
  providers: [{provide: NG_VALIDATORS, useExisting: StrongPasswordValidatorDirective, multi: true}]
})
export class StrongPasswordValidatorDirective implements Validator{

  constructor() { }

  validate(control: AbstractControl): ValidationErrors | null {
    return strongPasswordValidator()(control);
  }
}
