import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-game-limit',
  templateUrl: './game-limit.component.html',
  styleUrls: ['./game-limit.component.scss']
})
export class GameLimitComponent implements OnInit {

  @Output() gameLimit: EventEmitter<any> = new EventEmitter();
  limitOptions = [5, 10, 25, 50];
  limit = 10;
  constructor() { }

  ngOnInit(): void {
  }

  search(limit: number) {
    this.gameLimit.emit({"_limit": limit});
  }

}
