import { Directive } from '@angular/core';
import {NG_VALIDATORS, Validator, AbstractControl, ValidationErrors} from '@angular/forms';

import  {forbiddenEmailPasswordSameValidator} from '../validators/forbiddenEmailPasswordSameValidator';

@Directive({
  selector: '[appEmailAndPasswordValidator]',
  providers: [{provide: NG_VALIDATORS, useExisting: EmailAndPasswordValidatorDirective, multi: true}]
})
export class EmailAndPasswordValidatorDirective implements Validator {

  constructor() { }

  validate(control: AbstractControl): ValidationErrors | null {
    return forbiddenEmailPasswordSameValidator(control);
  }

}
