import {
  animation, state, trigger, animateChild, group,
  transition, animate, style, query
} from '@angular/animations';

export const transTrigger = (params: any) => {
  return trigger('myInsertRemoveTrigger', [
    transition(':enter', [
      style({ opacity: params.opatictyOut }),
      animate(params.timeIn, style({ opacity: params.opatictyIn })),
    ]),
    transition(':leave', [
      animate(params.timeOut, style({ opacity: params.opatictyOut }))
    ])
  ])
}

export const slideInAnimation =
  trigger('routeAnimations', [
    transition('* <=> *', [
      style({ position: 'absolute', width: '*' }),
      query(':enter, :leave', [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%'
        })
      ], { optional: true }),
      query(':enter', [style({ left: '-100%', opacity: 0 })], { optional: true }),
      query(':leave', animateChild(), { optional: true }),
      group([
        query(':leave', [animate('1s ease-out', style({ left: '100%', opacity: 0 }))], { optional: true }),
        query(':enter', [animate('1s ease-out', style({ left: '0%', opacity: 1 }))], { optional: true })
      ]),
      query(':enter', animateChild(), { optional: true })
    ])
  ]);
