import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormArray } from '@angular/forms';

import {ActivatedRoute, Router} from '@angular/router';
import {Subscription} from 'rxjs';

import {ApiService} from '../../../services/api.service';

import {minWordsLengthValidator} from '../../../validators/minWordsLengthValidator';
import {priceForSelectedGameNameValidator} from '../../../validators/priceForSelectedGameNameValidator';

@Component({
  selector: 'app-game-add',
  templateUrl: './game-add.component.html',
  styleUrls: ['./game-add.component.scss']
})
export class GameAddComponent implements OnInit {

  categoriesArr = ["Plataformas", "Acción", "Aventura", "Fiesta", "Tablero", "Deportes", "Tenis", "Arcade", "Estrategia", "Carreras", "Rol (RPG)", "Baloncesto", "Fútbol", "Golf", "Otros deportes", "Acción en primera persona (FPS)", "Disparos (Shooter)", "Animales", "Simulación", "Música", "Otros", "Puzle", "Lucha", "Salud y forma física", "Uso práctico"];
  addGameGroup = this.formBuilder.group({
    title: ['', [Validators.required]],
    price: [0, [Validators.required]],
    hasDemo: [false, [Validators.required]],
    description: ['', [Validators.required, minWordsLengthValidator(4)]],
    categories: this.formBuilder.array([
      this.formBuilder.control('')
    ]),
  }, {validators: priceForSelectedGameNameValidator})

  subscription = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private apiService: ApiService
  ) { }

  ngOnInit(): void {

  }

  get categories() {
    return this.addGameGroup.get('categories') as FormArray;
  }

  addCategory() {
    this.categories.push(this.formBuilder.control(''));
  }

  addGame()  {
    const payload = this.addGameGroup.value;
    this.addGameGroup.disable();
    this.subscription = this.apiService.addGame(payload)
    .subscribe(data => {
      this.router.navigate(['/games']);
    })
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
