import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';

import {ActivatedRoute, Router} from '@angular/router';
import {Subscription} from 'rxjs';

import {ApiService} from '../../../services/api.service';

import {minWordsLengthValidator} from '../../../validators/minWordsLengthValidator';
import {priceForSelectedGameNameValidator} from '../../../validators/priceForSelectedGameNameValidator';

@Component({
  selector: 'app-game-edit',
  templateUrl: './game-edit.component.html',
  styleUrls: ['./game-edit.component.scss']
})
export class GameEditComponent implements OnInit {

  editGameGroup = this.formBuilder.group({
    title: ['', [Validators.required]],
    price: [0, [Validators.required]],
    hasDemo: [false, [Validators.required]],
    description: ['', [Validators.required, minWordsLengthValidator(4)]]
  }, {validators: priceForSelectedGameNameValidator})
  subscription = new Subscription();

  fetchedGame = {title: '', price: '', description: ''};
  isSaved = false;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private apiService: ApiService
  ) { }

  ngOnInit(): void {
    this.subscription = this.route.data.subscribe((data:any) => {
      const fetchedGame = data.fetchedGame;
      this.fetchedGame = fetchedGame;
      this.editGameGroup.patchValue({
        title: fetchedGame.title,
        price: fetchedGame.price,
        hasDemo: fetchedGame.hasDemo,
        description: fetchedGame.description,
      });
    })
  }

  editGame()  {
    const payload = this.editGameGroup.value;
    const gameId:any = this.route.snapshot.paramMap.get('gameId');
    payload.id = parseInt(gameId);
    this.editGameGroup.disable();
    this.subscription = this.apiService.patchGame(payload)
    .subscribe(data => {
      this.isSaved = true;
      this.router.navigate(['/game', payload.id]);
    })
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  canDeactivate() {
    return this.isSaved || ((this.fetchedGame.title === this.editGameGroup.value.title) &&
           (this.fetchedGame.price === this.editGameGroup.value.price) &&
           (this.fetchedGame.description === this.editGameGroup.value.description));
  }

}
