import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import {Title} from '@angular/platform-browser';

import { ApiService } from '../../../services/api.service';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-games',
  templateUrl: './games.component.html',
  styleUrls: ['./games.component.scss']
})
export class GamesComponent implements OnInit {

  @ViewChild('gameLimit') gameLimitComponent: any;
  defaultLimit = 10;
  gamesObservable: Observable<any> = new Observable();
  queryParams:any = {};
  defaultOptions:any = {};
  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private titleService:Title
  ) { }

  ngOnInit(): void {
    this.titleService.setTitle(this.route.snapshot.data.title);
    this.gamesObservable = this.route.data.pipe(
      map((data:any) => ({games: data.response.body, headers: data.response.headers})),
    );
    this.defaultOptions = {"q": '', "_limit": 0};
  }

  ngAfterViewInit() {
    // get the defaults
    this.defaultOptions["_limit"] = this.defaultLimit;
    this.queryParams = this.defaultOptions;
  }

  fetchGames(params: any) {
    Object.keys(params).forEach(paramKey => {
      this.queryParams[paramKey] = params[paramKey];
    })
    this.gamesObservable = this.apiService.getFilteredGames(this.queryParams, true).pipe(
      map((data:any) => ({games: data.body, headers: data.headers})),
    )

  }

  getMaxPages(totalCount: any) {
    let limit = this.gameLimitComponent && this.gameLimitComponent.limit ? this.gameLimitComponent.limit : this.defaultLimit;
    return Math.ceil(totalCount / limit);
  }

  getCurrentPage() {
    return this.queryParams && this.queryParams['_page'] ? this.queryParams['_page'] : 1;
  }

}
