import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {gameRoutes} from './routes/games.routes';
import {platformRoutes} from './routes/platforms.routes';
import {myAccountRoutes} from './routes/my-account.routes';

import { LoginComponent } from './components/views/login/login.component';
import { ResetPasswordComponent } from './components/views/reset-password/reset-password.component';
import { PageNotFoundComponent } from './components/views/page-not-found/page-not-found.component';
import { PremiumDiscountsComponent } from './components/views/premium-discounts/premium-discounts.component';

import{PremiumAccessGuard} from './guards/premium-access.guard';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: '/platforms' },
  ...gameRoutes,
  ...platformRoutes,
  ...myAccountRoutes,
  {
    path: 'premium-discounts',
    component: PremiumDiscountsComponent,
    data: {title: 'Premium Access'},
    canActivate: [PremiumAccessGuard]
  },
  {
    path: 'login',
    component: LoginComponent,
    data: {title: 'Login'}
  },
  {
    path: 'reset-password',
    component: ResetPasswordComponent,
    data: {title: 'Login', animation: 'ResetPasswordPage'}
  },
  { path: 'consoles', redirectTo: '/platforms', pathMatch: 'full' },
  { path: '404', component: PageNotFoundComponent },
  { path: '**',  redirectTo: '/404', pathMatch: 'full'  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
