import { Component, OnInit, HostListener } from '@angular/core';
import {FormGroup, FormControl, Validators} from '@angular/forms';
import {Router} from '@angular/router';

import {ApiService} from '../../../services/api.service';

import {alphanumericValidator} from '../../../validators/alphanumericValidator';
import {uniquePlatformAsyncValidator} from '../../../validators/uniquePlatformAsyncValidator';

import {Subscription} from 'rxjs';
import { transTrigger } from '../../../animations/animation';

import {
  trigger,
  state,
  style,
  animate,
  transition,
  // ...
} from '@angular/animations';

@Component({
  selector: 'app-platform-add',
  templateUrl: './platform-add.component.html',
  styleUrls: ['./platform-add.component.scss'],
  animations: [
    trigger('validInvalid', [
      state('valid', style({
        'background-color': 'green'
      })),
      state('invalid', style({
        'background-color': 'red'
      })),
      transition('* => invalid', [
        animate('1.5s')
      ]),
      transition('invalid => valid', [
        animate('0.75s')
      ])
    ]),
    transTrigger({opatictyOut: 0, opatictyIn: 1, timeIn: '1s', timeOut: '1.2s'})
  ]
})
export class PlatformAddComponent implements OnInit {

  addFormGroup = new FormGroup({
    platformNameControl: new FormControl(
      '', [
        Validators.required,
        Validators.minLength(2),
        alphanumericValidator()
      ], [this.uniquePlatformAsyncValidator.validator()]
    )
  });

  subscription = new Subscription();
  isSaved = false;
  constructor(
    private apiService: ApiService,
    private router: Router,
    private uniquePlatformAsyncValidator: uniquePlatformAsyncValidator
  ) { }

  ngOnInit(): void {
  }

  addPlatform(){
    this.subscription = this.apiService.addPlatform({
      name: this.addFormGroup.value.platformNameControl
    })
    .subscribe(data => {
      this.isSaved = true;
      this.router.navigate(['/platforms']);
    })
  }

  @HostListener('window:beforeunload')
  canDeactivate() {
    return this.isSaved || (this.addFormGroup.untouched && this.addFormGroup.pristine);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
