import { Directive, Input } from '@angular/core';
import {NG_VALIDATORS, Validator, AbstractControl, ValidationErrors} from '@angular/forms';

import  {forbiddenEmailProviderValidator} from '../validators/forbiddenEmailProviderValidator';

@Directive({
  selector: '[appForbiddenEmailProviderValidator]',
  providers: [{provide: NG_VALIDATORS, useExisting: ForbiddenEmailProviderValidatorDirective, multi: true}]

})
export class ForbiddenEmailProviderValidatorDirective implements Validator{

  @Input('appForbiddenEmailProviderValidator') regexText = '';

  regex: RegExp;
  constructor() {
    console.log('constructor ', this.regexText)
    this.regex = new RegExp(this.regexText, 'i');
  }

  ngOnInit() {
    console.log('onInit ', this.regexText)
    this.regex = new RegExp(this.regexText, 'i');
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return forbiddenEmailProviderValidator(this.regex)(control);
  }
}
