import { Routes } from '@angular/router';

import { GamesComponent } from '../components/views/games/games.component';
import { GameComponent } from '../components/views/game/game.component';
import { GameEditComponent } from '../components/views/game-edit/game-edit.component';
import { GameAddComponent } from '../components/views/game-add/game-add.component';

import { CategoriesComponent } from '../components/views/categories/categories.component';
import { BestSellersComponent } from '../components/views/best-sellers/best-sellers.component';

import { GamesResolverService } from '../services/games-resolver.service';
import { GameResolverService } from '../services/game-resolver.service';

import { AdminAccessGuard } from '../guards/admin-access.guard';
import {AuthGuardGuard} from '../guards/auth-guard.guard';
import { PendingChangesGuard } from '../guards/pending-changes.guard';

export const gameRoutes: Routes = [
  {
    path: 'games',
    data: {
      title: 'Games - list'
    },
    children: [
      {
        path: '',
        component: GamesComponent,
        data: {animation: 'GamesList'},
        resolve: {
          response: GamesResolverService
        }
      },
      {
        path: 'categories',
        data: {animation: 'GamesCategories'},
        component: CategoriesComponent
      },
      {
        path: 'best-sellers',
        data: {animation: 'GamesBest'},
        component: BestSellersComponent
      }
    ]
  },
  {
    path: 'game',
    children: [
      {
        path: 'add',
        component: GameAddComponent,
        data: {animation: 'GameAdd'},
        canActivate: [AuthGuardGuard, AdminAccessGuard]
      },
      {
        path: ':gameId',
        data: {title: 'Game - detail'},
        resolve: {
          fetchedGame: GameResolverService
        },
        runGuardsAndResolvers: "always",
        children: [
          {
            path: 'edit',
            component: GameEditComponent,
            data: {title: 'Game - Edit', animation: 'GameEdit'},
            canActivate: [AuthGuardGuard, AdminAccessGuard],
            canDeactivate: [PendingChangesGuard]
          },
          {
            path: '',
            data: {animation: 'Game'},
            component: GameComponent,
          }
        ]
      },
    ]
  },
];
