import { Routes } from '@angular/router';

import { PlatformsComponent } from '../components/views/platforms/platforms.component';
import { PlatformComponent } from '../components/views/platform/platform.component';
import { PlatformAddComponent } from '../components/views/platform-add/platform-add.component';

import { PlatformResolverService } from '../services/platform-resolver.service';
import { PlatformsResolverService } from '../services/platforms-resolver.service';

import { AdminAccessGuard } from '../guards/admin-access.guard';
import {AuthGuardGuard} from '../guards/auth-guard.guard';
import { PendingChangesGuard } from '../guards/pending-changes.guard';

export const platformRoutes: Routes = [
  {
    path: 'platforms',
    component: PlatformsComponent,
    data: { title: 'Platform - list', animation: 'Platforms' },
    resolve: {
      platforms: PlatformsResolverService
    }
  },
  {
    path: 'platform',
    data: {animation: 'Platform'},
    children: [
      {
        path:'add',
        component: PlatformAddComponent,
        data: { title: 'Platform - Add platform' },
        canActivate: [AuthGuardGuard, AdminAccessGuard],
        canDeactivate: [PendingChangesGuard]
      },
      {
        path: ':id',
        component: PlatformComponent,
        data: { title: 'Platforms - detail' },
        resolve: {
          platform: PlatformResolverService
        }
      },
    ]
  },
  { path: 'consoles', redirectTo: '/platforms', pathMatch: 'full' },
];
