import { Injectable } from '@angular/core';

import {AsyncValidator, AbstractControl, ValidationErrors, AsyncValidatorFn} from '@angular/forms';
import {ApiService} from '../services/api.service';

import {Observable} from 'rxjs';
import {map, debounceTime, take, switchMap} from 'rxjs/operators';

@Injectable({ providedIn: 'root' })

export class uniquePlatformAsyncValidator implements AsyncValidator {

  constructor(private apiService: ApiService) {}

  validate(control: AbstractControl) : Observable<ValidationErrors | null>{
    return control.valueChanges.pipe(
      debounceTime(400),
      take(1),
      switchMap(_ =>
        this.apiService.getPlatformByName(control.value).pipe(
          map((platformsArr:any) =>
            (platformsArr.length > 0) ? {uniquePlatformAsync: {platformTaken: true}} : null)
          )
        )
    )


  }

  validator(): AsyncValidatorFn {
    return (control: AbstractControl): Observable<ValidationErrors | null> => {
      return this.validate(control);
    };
  }
}
