import { Injectable } from '@angular/core';
import {Resolve, RouterStateSnapshot,
  ActivatedRouteSnapshot, Router} from '@angular/router';

import {ApiService} from './api.service';
import {HttpErrorResponse} from '@angular/common/http';


import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class GameResolverService implements Resolve<any> {

  constructor(
    private apiService:ApiService,
    private router: Router,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    const gameId:any = route.paramMap.get('gameId');
    return this.apiService.getGame(gameId)
    .pipe(
      catchError((error) => this.handleError(error))
    )
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong.
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    this.router.navigate(['/404']);
    // Return an observable with a user-facing error message.
    return throwError('Something bad happened; please try again later.');
  }
}
